.toggle {
	isolation: isolate;
	height: 30px;
	width: 60px;
	border-radius: 15px;
	overflow: hidden;
	box-shadow: -8px -4px 8px 0px #fff, 8px 4px 12px 0px #d1d9e6, 4px 4px 4px 0px #d1d9e6 inset, -4px -4px 4px 0px #fff inset;
}
  
.toggle-state {
	display: none;
}
  
.indicator {
	height: 100%;
	width: 200%;
	background: #5caa6f;
	border-radius: 15px;
	-webkit-transform: translate3d(-75%, 0, 0);
	        transform: translate3d(-75%, 0, 0);
	transition: -webkit-transform .4s cubic-bezier(.85, .05, .18, 1.35);
	transition: transform .4s cubic-bezier(.85, .05, .18, 1.35);
	transition: transform .4s cubic-bezier(.85, .05, .18, 1.35), -webkit-transform .4s cubic-bezier(.85, .05, .18, 1.35);
	box-shadow: -8px -4px 8px 0px #fff, 8px 4px 12px 0px #d1d9e6;
}
  
.toggle-state:checked ~ .indicator {
	background: #e9aa35;
	-webkit-transform: translate3d(25%, 0, 0);
	        transform: translate3d(25%, 0, 0);
}
